/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.input-comp .control span.icon.is-right {
  pointer-events: all;
  cursor: pointer;
}

.input-comp .label {
  margin-top: 0.5rem;
  margin-right: 1.5rem;
}